/*========DISCOUNT*/
.discount {
  background: url("../../../img/discount/discount_bg.jpg") center no-repeat;
  background-size: cover;
  padding-bottom: 4px; }

.discount__title {
  font-size: 60px;
  font-weight: 700;
  line-height: 1;
  color: #3b3944;
  padding-top: 33px;
  padding-bottom: 27.2px; }
  .discount__title span {
    letter-spacing: 3px;
    color: #5774b8; }
  @media (min-width: 769px) and (max-width: 992px) {
    .discount__title {
      font-size: 54px; } }
  @media (min-width: 0) and (max-width: 480px) {
    .discount__title {
      padding-top: 23px;
      padding-bottom: 17.2px;
      font-size: 39px; } }

.discount__text {
  font-size: 28px;
  font-weight: 400;
  line-height: 1;
  color: #3b3944;
  margin-bottom: 47.2px; }
  @media (min-width: 0) and (max-width: 480px) {
    .discount__text {
      font-size: 19px;
      margin-bottom: 27.2px; } }

.discount__btn {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-bottom: 44px;
  max-width: 220px;
  z-index: 4; }
  @media (min-width: 0) and (max-width: 480px) {
    .discount__btn {
      margin: 0 auto 27.2px; } }

.discount__img {
  display: block;
  position: absolute;
  right: 40px;
  top: -24px; }
  @media (min-width: 769px) and (max-width: 992px) {
    .discount__img {
      right: -57.4px;
      top: 176px; }
      .discount__img img {
        max-width: 80%; } }
  @media (min-width: 480px) and (max-width: 769px) {
    .discount__img {
      right: -220px;
      top: 206px; }
      .discount__img img {
        max-width: 50%; } }
  @media (min-width: 0) and (max-width: 480px) {
    .discount__img {
      display: none; } }

/*====== COMPOSITION*/
.composition__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between; }
  @media (min-width: 992px) and (max-width: 1199px) {
    .composition__wrapper {
      padding-top: 137px; } }
  @media (min-width: 0) and (max-width: 1199px) {
    .composition__wrapper {
      flex-wrap: wrap; } }
  @media (min-width: 769px) {
    .composition__wrapper {
      padding-top: 67px; } }
  @media (min-width: 0) {
    .composition__wrapper {
      padding-top: 37px; } }

.composition__header {
  flex-basis: 100%; }

.composition__title {
  font-size: 54px;
  font-weight: 700;
  line-height: 1;
  color: #2e2c35;
  max-width: 498px;
  padding: 56px 0;
  text-align: center;
  margin: auto; }
  .composition__title span {
    letter-spacing: 3px;
    color: #5774b8; }
  @media (min-width: 0) and (max-width: 480px) {
    .composition__title {
      padding: 20px 0 30px;
      font-size: 30px; } }
  @media (min-width: 480px) and (max-width: 769px) {
    .composition__title {
      font-size: 44px;
      max-width: 100%; } }
  @media (min-width: 769px) and (max-width: 992px) {
    .composition__title {
      font-size: 46px;
      max-width: 100%; } }
  @media (min-width: 992px) and (max-width: 1199px) {
    .composition__title {
      max-width: 80%; } }
  @media (min-width: 1199px) {
    .composition__title {
      text-align: start;
      margin: 0; } }

.composition__img {
  display: none;
  padding-bottom: 15px; }
  @media (min-width: 1199px) {
    .composition__img {
      display: block; } }
  @media (min-width: 992px) and (max-width: 1199px) {
    .composition__img {
      display: none; } }

.types {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.types__item-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }
  @media (min-width: 0) and (max-width: 480px) {
    .types__item-row {
      flex-wrap: wrap; } }

.types__item {
  padding: 0 15px 70px;
  flex-basis: 265px; }
  @media (min-width: 0) and (max-width: 480px) {
    .types__item {
      padding: 0 0  30px;
      flex-basis: 100%; } }

.types__name {
  position: relative;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
  color: #2e2c35;
  padding-top: 6px;
  padding-bottom: 15px;
  margin-bottom: 34px; }
  @media (min-width: 0) and (max-width: 480px) {
    .types__name {
      margin-bottom: 24px; } }

.types__name::after {
  content: "";
  display: block;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: #e0e9ee;
  width: 192px; }

.types__img {
  margin-bottom: 22px; }
  @media (min-width: 0) and (max-width: 480px) {
    .types__img {
      margin: auto; }
      .types__img img {
        display: block;
        margin-left: auto;
        margin-right: auto; } }

.types__text {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3;
  color: #8e8c95; }

/*====== btn*/
.btn {
  width: 100%;
  margin: 0 auto;
  padding: 10px 52px;
  border: none;
  border-radius: 5px;
  background: repeating-linear-gradient(0deg, #3abde4 0%, #2d96cc 100%);
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  color: #fff;
  line-height: .8;
  z-index: 3; }

.btn__title {
  font-size: 23px;
  font-weight: 800;
  line-height: 1;
  z-index: 2; }

.btn__footer {
  position: absolute;
  height: 14.5px;
  bottom: -7px;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: #2d96cc;
  border-radius: 0 0 5px 5px;
  margin: 0 auto;
  box-shadow: 0px 6px 10px 0px #c1c5c7; }
